<template>
    <div>
        <TitleBar title="走路赚钱">
            <span
                @click="shareDot"
                class="title-bar-share"/>
        </TitleBar>
        <div class="running">
            <div class="running-bg">
                <div
                    @click="ToExchangeRecords"
                    class="convert-record">
                    <span class="convert-icon"/>
                    兑换记录
                </div>
                <div
                    @click="subscribeClock"
                    :class="[this.runningInfo.isSubscribe?'':'subscribe','alert']">
                    <div class="alert-clock"/>
                    <div class="alert-strip">
                        <p> {{ runningInfo.isSubscribe ? "已订阅":"打卡提醒" }}</p>
                    </div>
                </div>
                <div class="cloud"/>
                <div class="small-cloud"/>
                <PassMeter :running-info='runningInfo'/>
                <div
                    v-if="!gotStepAuth"
                    @click="openStepAuthBtn"
                    class="get-coin">
                    开启权限领金币
                </div>
                <div
                    v-else
                    @click="exchangeAtOnce"
                    :class="[
                        (this.runningInfo.isOverDailyLimit || this.exchangeAtOnceDisabled)
                            ?'get-coin-disabled'
                    :'','get-coin']">
                    {{ computeExchangeButtonText() }}
                </div>
            </div>
            <div class="running-banner">
                <span class="running-banner-img"/>
                <div class="running-banner-text">
                    <p>邀请好友</p>
                    <p>一起健康赚钱</p>
                </div>
                <div
                    @click="toInvite"
                    class="running-banner-btn">
                    去邀请
                </div>
            </div>
            <div class="today-data">
                <div class="today-data-title">
                    今日数据
                </div>
                <div class="today-data-content">
                    <div class="today-data-item">
                        <div class="data">
                            {{ this.mileage }}<div class="unit">公里</div>
                        </div>
                        <div class="item-event">
                            <span class="item-event-icon icon-mileage"/>
                            步行里程
                        </div>
                    </div>
                    <div class="today-data-item">
                        <div class="data">
                            {{ this.walktime.hour }}<div class="unit">时</div>
                            {{ this.walktime.minute }}<div class="unit">分</div>
                        </div>
                        <div class="item-event">
                            <span class="item-event-icon icon-walktime"/>
                            步行时间
                        </div>
                    </div>
                    <div class="today-data-item">
                        <div class="data">
                            {{ this.calarie }}<div class="unit">千卡</div>
                        </div>
                        <div class="item-event">
                            <span class="item-event-icon icon-calarie"/>
                            卡路里
                        </div>
                    </div>
                </div>
            </div>
            <div class="act-rule">
                <div class="act-rule-title">
                    活动规则
                </div>
                <div class="act-rule-content">
                    <div
                        class="act-rule-item"
                        :key='index'
                        v-for="(item,index) in actRule">
                        <div class="serial">
                            {{ index+1 }}
                        </div>
                        <div class="strip">
                            {{ item }}
                        </div>
                    </div>
                </div>
            </div>
            <div class="footer-banner">
                <div class="watermark"/>
            </div>
        </div>
        <div class="running-modal">
            <gray-mask
                :show="getStepAuthModal">
                <div class="step-auth modal-conter">
                    <div
                        @click="closeStepAuthModal"
                        class="step-auth-close"/>
                    <div
                        class="step-auth-title">
                        步数统计权限说明
                    </div>
                    <p>参与走路赚金币活动</p>
                    <p>需允许想看APP读取</p>
                    <p>系统运动步数内容</p>
                    <div
                        @click="openStepAuth"
                        class="step-auth-footer">
                        好的
                    </div>
                </div>
            </gray-mask>
            <transition name='fade'>
                <div
                    v-if='showSuccessGetToast'
                    class="running-toast"
                >
                    <div class="open-success modal-conter">
                        <div class="open-success-title">
                            兑换成功
                            <p class="open-success-coin">
                                +{{ this.runningInfo.coinCanExchange }}金币
                            </p>
                        </div>
                    </div>
                </div>
            </transition>
            <gray-mask
                :show="getCoinModal">
                <div class="get-coin-modal modal-conter">
                    <div
                        @click="closeCoinModal"
                        class="get-coin-modal-close"/>
                    <div class="get-coin-modal-content">
                        步数可兑换金币
                        <div class="coin">
                            {{ runningInfo.coinCanExchange }}
                        </div>
                    </div>
                    <div
                        @click="getAccountAftershare"
                        class="get-coin-modal-btn">
                        点击分享后到账
                    </div>
                    <div class="get-coin-modal-watermark">
                        我的金币 {{ runningInfo.totalCoin }}≈{{ transfer(runningInfo.totalCoin) }}元
                    </div>
                </div>
            </gray-mask>
            <Update
                v-if="updateModal"
                @close="()=>{this.updateModal = false}"
                @goSetting="goSetting"
                title='更新至最新客户端，升级后参与走路赚钱活动'>
                <template slot='content'>
                    <div class="update-modal-content"/>
                </template>
            </Update>
        </div>
    </div>
</template>

<script>
import { report, getActRunningInfo, exchange, subscribe } from '@/api/running';
import {
    doLogin,
    toast,
    isTakeOverBackByWeb,
    quitCurrentWebview,
    getUserAccountInfo,
    goSetting,
} from '@/utils/jsApiV2/client';
import {
    // 走路赚钱活动方法
    isSupportSteps,
    getTodaySteps,
} from '@/utils/jsApiV2/act';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { set, get } from '@/utils/cache';
import { getShareUrl } from '@/utils/share';
import GrayMask from '@/components/GrayMask';
import Update from '@/components/Update';
import conf from '@/config';
import TitleBar from './TitleBar';
import PassMeter from './PassMeter';
import config from './config';

const clientIsSupportSteps = 'clientIsSupportSteps';
const clientGetTodaySteps = 'clientGetTodaySteps';
const openStepCache = 'openSteps';
const todayShare = 'todayShare';
export default {
    name: 'Running',
    components: {
        TitleBar,
        PassMeter,
        GrayMask,
        Update,
    },
    data() {
        return {
            userInfo: getUserAccountInfo(),
            // 想看可以获取系统步数的兼容机型（包括第三方）
            supportStep: true,
            gotStepAuth: false,
            getStepAuthModal: false,
            showSuccessGetToast: false,
            getCoinModal: false,
            exchangeAtOnceDisabled: false,
            updateModal: false,
            shareToGetCoin: false,
            runningInfo: {
                isSubscribe: false,
                totalCoin: 0,
                todayStep: 0,
                remainSteps: 0,
                invitationCode: 'XIANGKAN',
            },
            alertClock: false,
            mileage: 0,
            walktime: {
                hour: '00',
                minute: '00',
            },
            calarie: 0,
            actRule: config.actRule,
        };
    },
    created() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: '走路赚钱',
            e_a: '曝光',
            e_n: '首页',
        });
        // 挂载客户端的方法到window上
        window.clientIsSupportSteps = this.clientIsSupportSteps;
        window.clientGetTodaySteps = this.clientGetTodaySteps;
        isSupportSteps(clientIsSupportSteps);
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
        document.removeEventListener('visibilitychange', this.listenVisiblityChange);
    },
    methods: {
        preCheck() {
            if (this.userInfo.isTourist || !this.userInfo.user_id) {
                doLogin('location.reload');
                return false;
            }
            if (conf.versionCode < 40890) {
                this.updateModal = true;
                return false;
            }
            return true;
        },
        clientIsSupportSteps(result) {
            if (!this.preCheck()) {
                return;
            }
            this.supportStep = result.supportStep;
            const openStepsFromCache = get(openStepCache);
            if (!openStepsFromCache) {
                this.getStepAuthModal = true;
            } else {
                this.gotStepAuth = true;
                // 获取今日步数 然后上报
                getTodaySteps(clientGetTodaySteps);
            }
        },
        clientGetTodaySteps(steps) {
            /*
                1. 步数不为0的时候 向服务端进行上报
                2. 小于0的时候 服务端处理兜底逻辑
                3. 等于0 服务端无法处理 交由前端来处理
            */
            this.runningInfo.todayStep = Math.max(steps, 0);
            if (steps !== 0) {
                report({
                //  兼容机型 取客户端传入的值  非兼容机型走服务端兜底逻辑
                    steps: this.supportStep ? steps : -1,
                }).then(() => {
                // 上报成功，获取首页数据
                    this.getActRunningInfo();
                }).catch((err) => {
                    if (err && err.response && err.response.status === 500) {
                        toast(err.response.msg);
                    } else {
                        this.$error();
                    }
                });
            } else {
                this.getActRunningInfo();
            }
        },
        getActRunningInfo() {
            getActRunningInfo().then((res) => {
                this.runningInfo = res;
                this.computeEventData();
            }).catch(() => {
                this.$error();
            });
        },
        computeExchangeButtonText() {
            const step = this.runningInfo.remainSteps;
            let text = '';
            if (!this.runningInfo.invitationCode) {
                return '正在获取最新步数';
            }
            if (step < 20 && !this.runningInfo.isOverDailyLimit) {
                this.exchangeAtOnceDisabled = true;
                text = `还差${20 - step}步领取1金币`;
            } else {
                this.exchangeAtOnceDisabled = false;
                text = this.runningInfo.isOverDailyLimit
                    ? '明天再来哦'
                    : `立即兑换${this.runningInfo.coinCanExchange}金币`;
            }
            return text;
        },
        computeEventData() {
            const todayStep = this.runningInfo.todayStep;
            this.mileage = (todayStep / 1000).toFixed(1);
            this.walktime = {
                // eslint-disable-next-line radix
                hour: parseInt((todayStep / 100) / 60),
                // eslint-disable-next-line radix
                minute: parseInt((todayStep / 100) % 60) };
            // eslint-disable-next-line radix
            this.calarie = (todayStep / 35).toFixed(1);
        },
        backKeyHandler() {
            // 有弹框出现的时候 关闭弹框 无弹框出现的时候 关闭当前webview
            if (this.getCoinModal) {
                this.getCoinModal = false;
            } else if (this.getStepAuthModal) {
                this.getStepAuthModal = false;
            } else {
                quitCurrentWebview();
            }
        },
        ToExchangeRecords() {
            if (!this.preCheck()) {
                return;
            }
            this.$router.push('/act/running/exchangeRecords');
        },
        openStepAuthBtn() {
            if (!this.preCheck()) {
                return;
            }
            this.openStepAuth();
        },
        closeStepAuthModal() {
            this.getStepAuthModal = false;
            this.$o2o({
                e_c: '走路赚钱',
                e_a: '点击',
                e_n: '关闭按钮',
            });
        },
        openStepAuth() {
            this.getStepAuthModal = false;
            this.gotStepAuth = true;
            set(openStepCache, true);
            // 获取今日步数 然后上报
            getTodaySteps(clientGetTodaySteps);
        },
        exchangeAtOnce() {
            if (!this.preCheck()) {
                return;
            }
            if (this.runningInfo.isOverDailyLimit) {
                toast('今日领取已达上限，明日再来哦');
                return;
            }
            if (this.exchangeAtOnceDisabled) {
                toast('每20步可兑换1金币');
                return;
            }
            this.$o2o({
                e_c: '走路赚钱',
                e_a: '点击',
                e_n: '立即兑换金币',
            });
            const todayCache = get(todayShare);
            const today = new Date().getDate();
            if (parseInt(todayCache, 10) !== today) {
                this.getCoinModal = true;
                this.$o2o({
                    e_c: '走路赚钱',
                    e_a: '曝光',
                    e_n: '分享金币到账兑换弹框',
                });
                document.addEventListener('visibilitychange', this.listenVisiblityChange);
                return;
            }
            this.exchange();
        },
        getAccountAftershare() {
            this.$o2o({
                e_c: '走路赚钱',
                e_a: '点击',
                e_n: '点击分享到账按钮',
            });
            set(todayShare, new Date().getDate());
            this.getCoinModal = false;
            this.shareToGetCoin = true;
            this.share();
        },
        listenVisiblityChange() {
            if (document.visibilityState === 'visible' && this.shareToGetCoin) {
                this.shareToGetCoin = false;
                this.exchange();
            }
        },
        exchange() {
            exchange({
                steps: this.runningInfo.remainSteps,
            }).then((res) => {
                if (res.code === 1) {
                    this.showSuccessGetToast = true;
                    setTimeout(() => {
                        this.showSuccessGetToast = false;
                        this.getActRunningInfo();
                    }, 3000);
                    return;
                }
                toast(res.msg);
            }).catch(() => {
                this.$error();
            });
        },
        subscribeClock() {
            if (!this.preCheck()) {
                return;
            }
            this.runningInfo.isSubscribe = !this.runningInfo.isSubscribe;
            subscribe({
                sub: this.runningInfo.isSubscribe ? 1 : 0,
            }).then(() => {
                if (this.runningInfo.isSubscribe) {
                    toast('订阅成功');
                } else {
                    toast('您已取消订阅');
                }
            }).catch(() => {
                this.$error();
            });
        },
        toInvite() {
            this.$o2o({
                e_c: '走路赚钱',
                e_a: '点击',
                e_n: '去邀请按钮',
            });
            this.share();
        },
        closeCoinModal() {
            this.getCoinModal = false;
        },
        transfer(totalCoin) {
            return (totalCoin / 10000).toFixed(2);
        },
        shareDot() {
            this.$o2o({
                e_c: '走路赚钱',
                e_a: '点击',
                e_n: '分享按钮',
            });
            this.share();
        },
        share() {
            if (this.userInfo.isTourist || !this.userInfo.user_id) {
                doLogin('location.reload');
                return;
            }
            const url = `https://feed.browser.miui.com/mobile-v2/act/invitation/newshare?shareId=${this.runningInfo.invitationCode}&share=running`;
            getShareUrl(url).then((res) => {
                const text = `今天我在【想看】走路赚钱啦！\n你也试试~\n${res}`;
                window.miui.shareText(text, 2);
            }).catch(() => {
                toast('分享失败，请升级当前app版本');
            });
        },
        goSetting() {
            goSetting();
        },
    },
};

</script>

<style scoped>
@import url(./index.css);
</style>
