<template>
    <div class="passmeter">
        <ul class="scaleStep">
            <li
                :key="index"
                class="scall"
                :style="handleStyle(index)"
                v-for="(item,index) in scallArr">
                <span/>
            </li>
            <div
                :key='index+100'
                class="number"
                v-for="(item,index) in numberArr">
                {{ index*2000 }}
            </div>
            <p
                class="archieve"
                :key="index+200"
                :style="handleStyle(index)"
                v-for="(item,index) in achieve">
                <span/>
            </p>
        </ul>
        <div class="people"/>
        <div class="todayStep">
            {{ todayStep }}<span>步</span>
        </div>
        <div class="banner">
            {{ this.bannerText }}
        </div>
    </div>
</template>

<script>
export default {
    name: 'PassMeter',
    props: {
        runningInfo: {
            type: Object,
            default() {
                return {};
            },
        },
    },
    data() {
        return {
            scallArr: [],
            numberArr: [],
            achieve: [],
            bannerText: '2000步可得100金币',
            todayStep: 0,
        };
    },
    mounted() {
        this.scallArr = Array.from({ length: 51 });
        this.numberArr = Array.from({ length: 6 });
    },
    watch: {
        runningInfo(v) {
            const step = Math.min(10000, v.todayStep);
            this.todayStep = v.todayStep;
            const percent = Math.ceil((step / 10000) * 51);
            this.achieve = Array.from({ length: percent });
            this.setBannerText(step, v.exchangedCoins);
        },
    },
    methods: {
        handleStyle(index) {
            const deg = ((index) * 4.8) - 30;
            return { transform: `rotate(${deg}deg)` };
        },
        setBannerText(value, coin) {
            let moreStep = 0;
            let moreCoin = 0;
            switch (true) {
            case value >= 1 && value < 2000 :
                moreStep = 2000 - value;
                moreCoin = 100 - coin;
                this.bannerText = `再走${moreStep}步可得${moreCoin}金币`;
                break;
            case value >= 2000 && value < 4000:
                moreStep = 4000 - value;
                moreCoin = 200 - coin;
                this.bannerText = `再走${moreStep}步可得${moreCoin}金币`;
                break;
            case value >= 4000 && value < 6000:
                moreStep = 6000 - value;
                moreCoin = 300 - coin;
                this.bannerText = `再走${moreStep}步可得${moreCoin}金币`;
                break;
            case value >= 6000 && value < 8000:
                moreStep = 8000 - value;
                moreCoin = 400 - coin;
                this.bannerText = `再走${moreStep}步可得${moreCoin}金币`;
                break;
            case value >= 8000 && value < 10000:
                moreStep = 10000 - value;
                moreCoin = 500 - coin;
                this.bannerText = `再走${moreStep}步可得${moreCoin}金币`;
                break;
            case value >= 1000:
                this.bannerText = '恭喜您成为今日健走达人';
                break;
            default:
                this.bannerText = '2000步可得100金币';
            }
        },
    },
};
</script>

